"use client";

import { useState } from "react";

export default function VerifyPage() {
  const [transactionId, setTransactionId] = useState("");
  const [verificationResult, setVerificationResult] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const verifyVote = () => {
    setError(null);
    setVerificationResult(null);

    if (!transactionId) {
      setError("Please enter a transaction ID.");
      return;
    }

    // Updated mock verification logic: accept any tx ID starting with "tx-"
    if (transactionId.startsWith("tx-")) {
      setVerificationResult("Vote verified successfully!");
    } else {
      setError("Unable to verify vote.");
    }
  };

  return (
    <div className="max-w-xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6">Verify Your Vote</h1>

      <label htmlFor="txid" className="block mb-2 font-semibold">
        Transaction ID:
      </label>
      <input
        id="txid"
        type="text"
        value={transactionId}
        onChange={(e) => setTransactionId(e.target.value)}
        className="w-full border border-gray-300 rounded px-3 py-2 mb-4"
      />

      <button
        onClick={verifyVote}
        className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
      >
        Verify
      </button>

      {verificationResult && (
        <div className="mt-4 p-3 bg-green-100 text-green-800 rounded">{verificationResult}</div>
      )}

      {error && (
        <div className="mt-4 p-3 bg-red-100 text-red-800 rounded">{error}</div>
      )}
    </div>
  );
}
